<script>
macgyver.register('mgCodeEditor', {
	requires: 'node_modules/ace-builds/src-noconflict/ace.js',
	title: 'Code Editor',
	icon: 'fal fa-code',
	category: 'Complex Inputs',
	preferId: true,
	config: {
		syntax: {type: 'mgChoiceDropdown', enum: ['text', 'json', 'javascript'], default: 'json'},
		theme: {type: 'mgChoiceDropdown', enum: ['chrome'], advanced: true, default: 'chrome', help: 'The syntax color scheme to use'},
		height: {type: 'mgText', default: '400px', help: 'The size of the editing window as a valid CSS measurement'},
	},
});

export default Vue.component('mgCodeEditor', {
	data: ()=> ({
		data: undefined,
	}),
	props: {
		config: Object,
		form: String,
	},
	watch: {
		config() {
			this.editor.getSession().setMode(`ace/mode/${this.$props.config.syntax}`);
			this.editor.setTheme(`ace/theme/${this.$props.config.theme}`);
		},
	},
	created() {
		this.$macgyver.inject(this);
	},
	beforeDestroy() {
		this.editor.destroy();
		this.editor.container.remove();
	},
	mounted() {
		this.editor = ace.edit(this.$el);
		this.editor.$blockScrolling = Infinity;

		this.editor.setValue(
			_.isObject(this.data) ? JSON.stringify(this.data, null, '\t')
			: this.data ? this.data
			: ''
		, 1);
		this.editor.setOptions({
			showPrintMargin: false,
		});
		this.editor.on('change', ()=> this.$macgyver.forms.emit(this.form, 'mgChange', this.$props.config.id, this.editor.getValue()));

		this.$nextTick(()=> this.editor.resize());
	},
	render(h) {
		return h('div', {
			attrs: {
				class: 'mg-code-editor',
				style: `height: ${this.$props.config.height}; width: 100%`,
			},
		});
	},
});
</script>

<style>
.mg-code-editor {
	border: 1px solid #f0f0f0;
	border-radius: 5px;
}
</style>
